:root {
    --dark-color: #212529;
    --gray-color: #aaaaaa;
    --main-color: #5285c5;
    --white-color: #fff;
}

*{ font: 2.5vmin 'Century Gothic', Helvetica, Arial, sans-serif; }

a { text-decoration: none; }

.active { color: var(--main-color)!important; }

a svg { color: var(--main-color)!important; }

h1,h2,h3,h4,h5,h6 { color: var(--main-color)!important; }

span { color: var(--main-color)!important; }

.icon { font-size: 8vmin; }

.btn-login {
    color: var(--main-color);
    border-color: var(--main-color)!important;
}
.btn-login:hover {
    color: var(--white-color);
    background-color: var(--main-color);
}
.btn-inmueble, .btn-buscar {
    color: var(--main-color);
    background-color: var(--white-color);
    border-color: var(--main-color);
}
.btn-inmueble:hover, .btn-buscar:hover {
    color: var(--dark-color);
    background-color: var(--main-color);
}

input::placeholder { color: var(--gray-color); }
input[type=text]{
    background-color: white;
    color: var(--main-color);
    border-color: var(--gray-color)!important;
}
input[type=password]{
    background-color: white;
    color: var(--main-color);
    border-color: var(--gray-color)!important;
}

button { background-color:var(--dark-color); color:var(--white-color); }
button:hover{ color:var(--dark-color); background-color:var(--gray-color); }

.alertMessage { color: var(--gray-color); }